/* Arrows */
.slick-dotted .slick-dots {
  float: left;
  margin: 20px 0 0;
  text-align: center;
  width: 100%;
}
.slick-dots li {
  width: 14px;
  height: 14px;
  background: none;
  border: 2px solid #ffffff;

  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
  border-radius: 50%;

  margin: 0 7px;
  margin-top: 0px;
  margin-top: 20px;
  float: none;
  display: inline-block;
}
.slick-dots li button {
  width: 6px;
  height: 6px;
  background: #ffffff;

  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
  border-radius: 50%;

  margin-top: 2px;
  margin-right: 2px;
  opacity: 0;
  padding: 0;
  color: transparent;
}
.slick-dots li.slick-active button {
  opacity: 1;
  background: #ffffff;
}
.slick-prev,
.slick-next {
  background: none;
  border: none;

  color: transparent;
  cursor: pointer;
  display: block;
  font-size: 0;
  height: 40px;
  line-height: 0;
  margin-top: 0;
  outline: medium none;
  padding: 0;
  position: absolute;
  top: 50%;
  width: 100px;
  margin: 0 40px;
  z-index: 11;
  text-align: center;
}
.slick-prev:hover:before,
.slick-prev:focus:before,
.slick-next:hover:before,
.slick-next:focus:before {
  opacity: 1;
}
.slick-prev.slick-disabled:before,
.slick-next.slick-disabled:before {
  opacity: 0.25;
}
.slick-prev::before,
.slick-next::before {
  color: #ffffff;
  font-family: "lineawesome";
  font-size: 30px;
  left: 0;
  line-height: 1;
  opacity: 0.75;
  position: absolute;
  top: 9px;
  width: 100%;
}
.slick-prev {
  left: -25px;
}
[dir="rtl"] .slick-prev {
  right: -25px;
  left: auto;
}
.slick-prev::before {
  content: "\f120";
}
[dir="rtl"] .slick-prev:before {
  content: "\f120â€™";
}
.slick-next {
  right: -25px;
}
[dir="rtl"] .slick-next {
  right: auto;
  left: -25px;
}
.slick-next:before {
  content: "\f121";
}
[dir="rtl"] .slick-next:before {
  content: "\f121";
}
.slick-slider {
  position: relative;
  display: block;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-touch-callout: none;
  -khtml-user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}
.slick-list {
  position: relative;
  display: block;
  overflow: hidden;
  margin: 0;
  padding: 0;
}
.slick-list:focus {
  outline: none;
}
.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}
.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}
.slick-track {
  position: relative;
  top: 0;
  left: 0;
  display: block;
}
.slick-track:before,
.slick-track:after {
  display: table;
  content: "";
}
.slick-track:after {
  clear: both;
}
.slick-loading .slick-track {
  visibility: hidden;
}
.slick-slide {
  display: none;
  float: left;
  height: 100%;
  min-height: 1px;
}
.slideHome {
  height: 700px !important;
}
[dir="rtl"] .slick-slide {
  float: right;
}
.slick-slide img {
  display: block;
}
.slick-slide.slick-loading img {
  display: none;
}
.slick-slide.dragging img {
  pointer-events: none;
}
.slick-initialized .slick-slide {
  display: block;
}
.slick-loading .slick-slide {
  visibility: hidden;
}
.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}
.slick-arrow.slick-hidden {
  display: none;
}

